.products {
  &__container {
    display: block;
    text-align: center;
    padding: 60px 0;
  }
  &__label {
    font-weight: 800;
    font-size: 18px;
    line-height: 1.22;
    color: $title-color-black;
    text-transform: uppercase;
    margin-bottom: 12px;
    @include tablet {
      font-size: 22px;
    }
    @include desktop {
      font-size: 30px;
    }
  }
  &__titel {
    font-family: $title-fonts;
    font-weight: 400;
    font-size: 30px;
    line-height: 1.27;
    text-align: center;
    color: $title-color-black;
    text-transform: uppercase;
    margin-bottom: 140px;
    @include tablet {
      font-size: 34px;
      line-height: 1.26;
      margin-bottom: 186px;
    }
    @include desktop {
      margin-bottom: 278px;
      font-size: 36px;
      line-height: 1.25;
      letter-spacing: 0.06em;
    }
  }
  &__list {
    @include flex-align;
    flex-direction: column;
    justify-content: center;
    @include tablet {
      flex-direction: row;
      gap: 16px;
    }
  }

  &__item {
    position: relative;
    @include flex-align;
    flex-direction: column;
    justify-content: center;
    border-radius: 24px;
    padding: 123px 25px 16px;
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
    @media screen and (max-width: 767px) {
      max-width: 280px;
      min-height: 420px;
    }
    @include tablet {
      width: 224px;
      height: 428px;
      padding: 108px 26px 12px;
    }
    @include desktop {
      padding: 165px 63px 60px;
      width: 370px;
      height: 536px;
    }
  }

  &__item--green::before {
    content: '';
    position: absolute;
    background-size: cover;
    width: 200px;
    height: 200px;
    top: 0%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    background: linear-gradient(
        0deg,
        rgba(156, 190, 143, 0.7),
        rgba(156, 190, 143, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.34219px 20.0332px rgba(10, 136, 23, 0.25);
    @include desktop {
      width: 302px;
      height: 302px;
    }
  }

  &__item--green::after {
    content: '';
    position: absolute;

    background-image: url(../images/products_img_cream_1x.png);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/products_img_cream_2x.png);
      background-position: center;
      background-size: cover;
    }
    background-size: cover;
    width: 200px;
    height: 200px;
    top: -11px;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    @include desktop {
      width: 302px;
      height: 302px;
    }
  }

  &__item:hover {
    transform: translateY(-5%);
    box-shadow: rgba(226, 196, 63, 0.25) 0px 13px 47px -5px,
      rgba(180, 71, 71, 0.3) 0px 8px 16px -8px;
  }

  &__item--coffee::before {
    content: '';
    position: absolute;
    background-size: cover;
    width: 200px;
    height: 200px;
    top: 0%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    background: linear-gradient(
        0deg,
        rgba(248, 210, 78, 0.7),
        rgba(248, 210, 78, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.40397px 20.2649px rgba(153, 114, 23, 0.25);
    @include desktop {
      width: 302px;
      height: 302px;
    }
  }

  &__item--coffee::after {
    content: '';
    position: absolute;

    background-image: url(../images/products_img_coffee_1x.png);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/products_img_coffee_2x.png);
      background-position: center;
      background-size: cover;
    }
    background-size: cover;
    width: 200px;
    height: 200px;
    top: -11px;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    @include desktop {
      width: 302px;
      height: 302px;
    }
  }

  &__item--milkshakes::before {
    content: '';
    position: absolute;
    background-size: cover;
    width: 200px;
    height: 200px;
    top: 0%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    background: linear-gradient(
        0deg,
        rgba(213, 117, 63, 0.7),
        rgba(213, 117, 63, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.41333px 20.3px rgba(0, 0, 0, 0.25);
    @include desktop {
      width: 302px;
      height: 302px;
    }
  }

  &__item--milkshakes::after {
    content: '';
    position: absolute;

    background-image: url(../images/products_img_milkshakes_1x.png);
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/products_img_milkshakes_2x.png);
      background-position: center;
      background-size: cover;
    }
    background-size: cover;
    width: 200px;
    height: 200px;
    top: -11px;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    @include desktop {
      width: 302px;
      height: 302px;
    }
  }

  &__item:not(:last-child) {
    @media screen and (max-width: 767px) {
      margin-bottom: 160px;
    }
  }

  &__item--green {
    background-color: $products-bg-color-green;
  }

  &__item--coffee {
    background-color: $products-bg-color-yellow;
  }

  &__item--milkshakes {
    background-color: $products-bg-color-coffee;
  }

  &__name {
    font-size: 20px;
    line-height: 1.2;
    text-transform: uppercase;
    margin-bottom: 40px;
    letter-spacing: 0.04em;
    color: $title-color-black;
    @include tablet {
      font-size: 22px;
      line-height: 1.23;
    }

    @include desktop {
      font-size: 30px;
      margin-bottom: 66px;
    }
  }
  &__descr {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.76;
    color: $title-color-black;
    @include desktop {
      font-size: 16px;
    }
  }

  &__btn {
    background-color: transparent;
    border: none;
    width: 40px;
    height: 16px;
    fill: $title-color-black;
    margin-top: auto;

